#paginator .pagination > li > a[data-v-6f61e6f3],
#paginator .pagination > li > span[data-v-6f61e6f3] {
    background-color: #FFF;
    border: 3px solid #FB923B;
    color: #FB923B !important;
    border-radius: 10px;
}
#paginator .pagination > li.active > a[data-v-6f61e6f3],
#paginator .pagination > li.active > span[data-v-6f61e6f3] {
    background-color: #FFF;
    color: #FB923B !important;
    font-weight: bold;
}
#paginator .pagination > li.disabled > a[data-v-6f61e6f3],
#paginator .pagination > li.disabled > span[data-v-6f61e6f3] {
    color: rgba(32, 32, 32, .7) !important;
}



