/* Panel Bootstrap */
.panel { border-radius: 10px; }

/* Card */
.card {
    border-radius: 10px;
}

.card-title {
    color: #000;
}

.card-header.cover {
    width: 100%; height: 150px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
    border-radius: 10px 10px 0 0;
    padding: 20px !important;
}

/* Border Inside Card */

.border-inside-card {
    border: 1px solid rgba(255, 255, 255, .1);
    padding: 7px 0px;
    border-radius: 10px;
}

.border-inside-card.strong {
    border: 1px solid #FB923B;
}

.card {
  position: relative;
  background: #fff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  margin-bottom: 30px;
}

.card .card-header,
.card .card-footer {
  position: relative;
}

.card .card-footer { border: 1px solid rgba(0, 0, 0, .1); }

.card .card-footer.flex { border: none; }

.card-footer.flex {
    display: flex;
    margin: 0 -5px;
}

.card-footer.flex .btn {
    width: 100%;
    margin: 0 5px;
}

.card .card-footer:before,
.card .card-footer:after {
    content: '';
    background-color: rgba(0, 0, 0, .1);
    height: 1px;
    width: calc(50% - 5px);
    position: absolute;
    top: 0;
}

.card .card-footer:before { left: 5px; }
.card .card-footer:after { right: 5px; }

.card .card-header.ch-alt {
    padding: 23px 26px;
}

@media screen and (min-width: 768px) {
  .card .card-header:not(.ch-alt) {
    padding: 23px 25px;
  }
}

@media screen and (max-width: 991px) {
  .card .card-header:not(.ch-alt) {
    padding: 18px;
  }
}

.card .card-header h2 {
  margin: 0;
  line-height: 100%;
  font-size: 17px;
  font-weight: 400;
}

.card .card-header h2 small {
  display: block;
  margin-top: 8px;
  color: #AEAEAE;
  line-height: 160%;
}

@media screen and (min-width: 768px) {
  .card .card-header.ch-alt {
    padding: 23px 26px;
  }
}

@media screen and (max-width: 991px) {
  .card .card-header.ch-alt {
    padding: 18px 18px 28px;
  }
}

.card .card-header.ch-alt:not([class*="bgm-"]) {
  background-color: #dadada;
}

.card .card-header[class*="bgm-"] h2,
.card .card-header[class*="bgm-"] h2 small {
  color: #fff;
}

.card .card-header .actions {
  position: absolute;
  right: 10px;
  z-index: 2;
  top: 15px;
}

.card .card-header .btn-float {
  right: 25px;
  bottom: -23px;
  z-index: 1;
}

@media screen and (min-width: 768px) {
  .card .card-body.card-padding,
  .card .card-footer.card-padding {
    padding: 23px 26px;
  }
}

@media screen and (max-width: 991px) {
  .card .card-body.card-padding,
  .card .card-footer.card-padding {
    padding: 18px;
  }
}

.card .card-body.card-padding-sm {
  padding: 15px;
}

.card-header:not(.ch-alt):not([class*="bgm-"]) + .card-padding {
  padding-top: 0;
}



